import Phaser from '../../phaser.min.js';

class QuestionF1 extends Phaser.Scene {
  constructor () {
    super({
      key: 'QuestaoF1'
    });
  }
  init(data){
    this.Question = data.Question;
    this.currentQuestion = data.id;
    console.log(this.Question);
  }
  create () {
    var disableButtons = false;
    var max = this.Question.QtdeItens;
    var win = 0;
    var dialog = this.rexUI.add.dialog({
      x: 500,
      y: 350,
      width: 500,

      background: this.rexUI.add.roundRectangle(0, 0, 100, 100, 20, 0x1565c0),

      title: createLabel(this, 'Questo F1').setDraggable(),

      toolbar: [
        createLabel(this, 'X')
      ],

      content: this.add.image(0, 0, 'f1-q'+this.currentQuestion),

      actions: createOptionLabel(this, max),

      space: {
        left: 20,
        right: 20,
        top: -20,
        bottom: -20,

        title: 25,
        // titleLeft: 30,
        content: 25,
        description: 25,
        // descriptionLeft: 20,
        // descriptionRight: 20,
        choices: 25,

        // toolbarItem: 5,
        choice: 15,
        action: 100
      },

      expand: {
        title: false
        // content: false,
        // description: false,
        // choices: false,
        // actions: false,
      },

      align: {
        // title: 'center',
        // content: 'right',
        // description: 'left',
        // choices: 'left',
        actions: 'center' // 'center'|'left'|'right'
      },

      click: {
        mode: 'release'
      }
    })
      .layout()
    // .drawBounds(this.add.graphics(), 0xff0000)
      .popUp(1000);

    this.print = this.add.text(0, 0, '');
    dialog
      .on('button.click', function (button, groupName, index) {
        //this.print.text += groupName + '-' + index + ': ' + button.text + '\n';
        if (button.text === 'X') {
          // this.scene.start('Menu');
          let a = this.scene.get('Menu');
          if(win)
            a.correct();
          else
            a.wrong();
          this.scene.resume('Menu');
          this.scene.stop();
        }
        if (groupName === 'actions' && !disableButtons) {
          var questionImage = dialog.getElement('content');
          questionImage.setTexture('f1-r'+this.currentQuestion);
          disableButtons = true;
          if(this.Question.Itens[index].Resposta === "1"){
              button.getElement('background').setTint(0x00ff00);
              win = 1;
          }
          else{
              button.getElement('background').setTint(0xff0000);
              win = 0;
          }
        }
      }, this)
      .on('button.over', function (button, groupName, index) {
        if(groupName === 'actions' && !disableButtons)
          button.getElement('background').setScale(0.45);
      })
      .on('button.out', function (button, groupName, index) {
        if(groupName === 'actions' && !disableButtons)
          button.getElement('background').setScale(0.35);
      });
  }

  update () {}
}

var createLabel = function (scene, text) {
  return scene.rexUI.add.label({
    width: 40, // Minimum width of round-rectangle
    height: 40, // Minimum height of round-rectangle

    background: scene.rexUI.add.roundRectangle(0, 0, 0, 0, 20, 0x5e92f3),

    text: scene.add.text(0, 0, text, {
      fontSize: '24px'
    }),

    space: {
      left: 10,
      right: 10,
      top: 10,
      bottom: 10
    }
  });
};

var createOptionLabel = function (scene, max) {
  let actions = [];
  let btnName=['ca', 'cb', 'cc', 'cd', 'ce'];
  for (let index = 0; index < max; index++) {
      actions.push(scene.rexUI.add.label({
        width: 80, // Minimum width of round-rectangle
        height: 80, // Minimum height of round-rectangle
        // background: scene.rexUI.add.roundRectangle(0, 0, 0, 0, 20, 0x5e92f3),
        background: scene.add.image(0,0,btnName[index]),
        space: {
          left: 50,
          right: 50,
          top: 10,
          bottom: 10
        },
      }));
  }
  return actions;
};

export default QuestionF1;
