import Phaser from '../../phaser.min.js';

class MenuScene extends Phaser.Scene {
  constructor () {
    super('Menu');
    this.x = 10;
    this.currentQuestion = 1;
  }

  correct() {
    this.add.text(20, 10 + this.x, 'Acertou a questo ' + this.currentQuestion);
    this.x = this.x + 10;
    this.currentQuestion += 1;
    this.game.global.sendData.qtdacertos += 1;
    this.add.text(20, 10 + this.x, 'Total de Acertos: ' + this.game.global.sendData.qtdacertos);
    this.x = this.x + 20;
    this.canThrow = true;
    this.knife.y = this.game.config.height/5*4;
    this.knife.rotation = 0;
  }

  wrong() {
    this.add.text(20, 10 + this.x, 'Errou a questo ' + this.currentQuestion);
    this.x = this.x + 10;
    this.currentQuestion += 1;
    this.add.text(20, 10 + this.x, 'Total de Acertos: ' + this.game.global.sendData.qtdacertos);
    this.x = this.x + 20;
    this.currentQuestion = 1;
    this.scene.start();
  }


  create () {
    this.add.text(20, 10, 'MenuScene');
    var gameOptions = this.game.global.gameOptions;
    console.log(gameOptions);

    this.currentRotationSpeed = gameOptions.rotationSpeed;
    this.newRotationSpeed = gameOptions.rotationSpeed;

    // 
    this.canThrow = true;

    // 
    this.knifeGroup=this.add.group();


    // 
    this.knife = this.add.sprite(this.game.config.width/2, this.game.config.height/5*4,"knife");
    this.target = this.add.sprite(this.game.config.width/2,150,"target");

    // ,
    this.target.depth = 1;

    // 
    this.input.on("pointerdown", this.throwKnife, this);

        //v1.2 
        var timedEvent = this.time.addEvent({
            delay: gameOptions.changeTime,
            callback: this.changeSpeed,
            callbackScope: this,
            loop: true
        });
  }


    	//v1.2 
      changeSpeed(){
        var gameOptions = this.game.global.gameOptions;

        //v1.2 +1-1
        var sign = Phaser.Math.Between(0, 1) == 0 ? -1 : 1;

        //v1.2 [-]
        var variation = Phaser.Math.FloatBetween(-gameOptions.rotationVariation, gameOptions.rotationVariation);

        //v1.2 
        this.newRotationSpeed = (this.currentRotationSpeed + variation) * sign;

        //v1.2 
        this.newRotationSpeed = Phaser.Math.Clamp(this.newRotationSpeed, -gameOptions.maxRotationSpeed, gameOptions.maxRotationSpeed);
    }

	// 
	throwKnife(){
    var gameOptions = this.game.global.gameOptions;

		// 
		if(this.canThrow){
			
			this.canThrow = false;

			// 
			this.tweens.add({
				// 
				targets:[this.knife],
				// 
				y: this.target.y+this.target.width/2,
        rotation: 0,
				 // 
				duration: gameOptions.throwSpeed,

				// 
				callbackScope: this,

				 // 
				onComplete: function(tween){

					//v1.1  
					var legalHit = true;
					//v1.1  
					var children = this.knifeGroup.getChildren();
					//v1.1 
                    for (var i=0; i<children.length; i++){
 
                        //v1.1 
                        if(Math.abs(Phaser.Math.Angle.ShortestBetween(this.target.angle, children[i].impactAngle)) < gameOptions.minAngle){
 
                            //v1.1 
                            legalHit = false;
 
                            //v1.1 
                            break;
                        }
                    }
                    // 
                    if(legalHit){

						// 
						this.canThrow= true;

						// 
						var knife = this.add.sprite(this.knife.x, this.knife.y, "knife");
						//v1.1 
                        knife.impactAngle = this.target.angle;
                        // 
						this.knifeGroup.add(knife);

						// 
						this.knife.y = this.game.config.height/5*4;
					}
					else
					{
						//v1.1 
                        this.tweens.add({
 
                            //v1.1 
                            targets: [this.knife],
 
                            y: this.game.config.height + this.knife.height,
 
                            //v1.1 
                            rotation: 5,

                            //v1.1  
                            duration: gameOptions.throwSpeed * 4,
 
                            //v1.1 
                            callbackScope: this,
 
                            //v1.1 
                            onComplete: function(tween){
 
                                //v1.1 
                                //this.scene.start();
                                this.scene.pause();
                                this.scene.launch('QuestaoF1', {id:this.currentQuestion, Question: this.game.global.Questions[this.currentQuestion-1] });
                                // 
                            }
                        });
					}
				}

			});
		}

	}

	//  v1.2 
	//update(){
	update(time, delta){
    if(this.currentQuestion > 5){
      this.add.text(20, 10 + this.x, 'FIM DE JOGO');
      this.scene.pause();
    }
    var gameOptions = this.game.global.gameOptions;

		//v1.2  
		//this.target.angle += gameOptions.rotationSpeed;
		this.target.angle += this.currentRotationSpeed;

		// 
		var children = this.knifeGroup.getChildren();

		// 
		for (var i=0; i<children.length; i++){

			//v1.2  
			//children[i].angle += gameOptions.rotationSpeed;
			children[i].angle += this.currentRotationSpeed;

			 // 
			var radians = Phaser.Math.DegToRad(children[i].angle + 90);

			// 
			children[i].x = this.target.x + (this.target.width/2)*Math.cos(radians);
            children[i].y = this.target.y + (this.target.width/2)*Math.sin(radians);
		}
		//v1.2 
        this.currentRotationSpeed = Phaser.Math.Linear(this.currentRotationSpeed, this.newRotationSpeed, delta / 1000);


}
};
export default MenuScene;
