import Phaser from '../../phaser.min.js';
import target from './../../assets/images/target.png';
import knife from './../../assets/images/knife.png';
import f1q1 from './../../assets/images/f1-q1.png';
import f1r1 from './../../assets/images/f1-r1.png';
import f1q2 from './../../assets/images/f1-q2.png';
import f1r2 from './../../assets/images/f1-r2.png';
import f1q3 from './../../assets/images/f1-q3.png';
import f1r3 from './../../assets/images/f1-r3.png';
import f1q4 from './../../assets/images/f1-q4.png';
import f1r4 from './../../assets/images/f1-r4.png';
import f1q5 from './../../assets/images/f1-q5.png';
import f1r5 from './../../assets/images/f1-r5.png';
import ca from './../../assets/images/ca.png';
import cb from './../../assets/images/cb.png';
import cc from './../../assets/images/cc.png';
import cd from './../../assets/images/cd.png';
import ce from './../../assets/images/ce.png';



class BootScene extends Phaser.Scene {
  constructor () {
    super('Boot');
  }

  preload () {
    // Load all images sounds sprite.
    this.load.image('f1-q1', f1q1);
    this.load.image('f1-r1', f1r1);
    this.load.image('f1-q2', f1q2);
    this.load.image('f1-r2', f1r2);
    this.load.image('f1-q3', f1q3);
    this.load.image('f1-r3', f1r3);
    this.load.image('f1-q4', f1q4);
    this.load.image('f1-r4', f1r4);
    this.load.image('f1-q5', f1q5);
    this.load.image('f1-r5', f1r5);
    this.load.image('ca', ca);
    this.load.image('cb', cb);
    this.load.image('cc', cc);
    this.load.image('cd', cd);
    this.load.image('ce', ce);
  this.load.image("target",target);
  this.load.image("knife",knife);

  }

  create () {
    this.add.text(10, 10, 'Phaser 3 Questions Module Test');
    console.log(this.game.global._response)
      this.scene.switch('Menu');
  }
}

export default BootScene;
